@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fraunces:ital@1&display=swap);
.App {
	text-align: center;
}

body {
	background: linear-gradient(to left, #ccf5f8, #8bf6fd);
}

.title {
	font-family: 'Zilla Slab', serif;
	font-weight: 300;
}

button {
	border: 0;
	padding: 8px 20px;
	margin: 5% 2px 5px 0;
	border-radius: 5px;
	font-weight: 600;
	font-size: 15px;
	background-color: #f1d451;
	color: rgb(0, 0, 0);
	cursor: pointer;
	outline: none;
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.9);
}

button:active {
	transform: scale(0.98);
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
}

.quotes {
	font-family: 'Fraunces', serif;
	margin: 10%;
	margin-bottom: 5%;
	font-size: 22px;
}

.author {
	font-size: 20px;
	font-family: 'Fraunces', serif;

}

